'use strict';

/**
* Return an object's own property descriptors.
*
* @module @stdlib/utils-property-descriptors
*
* @example
* import getOwnPropertyDescriptors from '@stdlib/utils-property-descriptors';
*
* var obj = {
*   'foo': 'bar',
*   'beep': 'boop'
* };
*
* var keys = getOwnPropertyDescriptors( obj, 'foo' );
* // returns {...}
*/

// MODULES //

import HAS_BUILTIN from './has_builtin.js';
import builtin from './builtin.js';
import polyfill from './polyfill.js';


// MAIN //

var main;
if ( HAS_BUILTIN ) {
	main = builtin;
} else {
	main = polyfill;
}


// EXPORTS //

export default main;
